
import React,{useState} from 'react';
import axios from 'axios';
import {Bars} from "react-loader-spinner"
import pic from "../images/mpesa.svg"
import "bootstrap/dist/css/bootstrap.min.css"
import "./Form.css";
import Validation from './Validation';
import "font-awesome/css/font-awesome.min.css"
import {useToast} from 'rc-toastr'
import "rc-toastr/dist/index.css"

function Form(){
    const {toast}=useToast()
    const [values,setValues]=useState({payBill:"",consumerKey:"",consumerSecret:"",confirmationUrl:"",validationUrl:""})
    const [errors,setErrors]=useState({});
    const [loading,setLoading]=useState(false)
    const handleInput=(value)=>{
        const newObj={...values,[value.target.name]:value.target.value}
        setValues(newObj);
    }
    const handleSubmit=async (e)=>{
        e.preventDefault();
        setErrors(Validation(values));
        if(Object.keys(errors).length ===0){
            setLoading(true)
            await axios.post("https://nirejeste.herokuapp.com/register/mpesa/callback/urls",{values})
            .then(response=>{
                setLoading(false)
                if(response.data.ResponseCode ==='0'){
                    toast.success(`${response.data.ResponseDescription}:Your urls have been registered successfully.`)
                    e.target.reset();
                }else{
                    toast.error(response.data.errorMessage)
                }
            })
            .catch(error=>{
                setLoading(false)
                toast.error("Check your Internet Connection")
            })
        }
    }
    return (
        <section className="w3l-login">
		<div className="overlay">
			<div className="wrapper">
				<div className="form-section">
                    <center> <img src={pic} className='img-responsive' alt='mpesa logo' width={250}/></center>
					<h3>Register Callback URL</h3>
					<form action="#" method="post" className="signin-form" onSubmit={handleSubmit}>
						<div className='parent'>
                            <div className="form-input">
                                <input type="number" name="payBill" placeholder="Short Code" autoFocus onChange={handleInput}></input>
                            </div>
                            {errors.payBill && <small className="text-danger"><i className="fa fa-exclamation-circle"></i> {errors.payBill}</small>} 
                        </div>
						<div className='parent'>
                            <div className="form-input">
                                <input type="text" name="consumerKey" placeholder="Consumer Key"  onChange={handleInput}></input>
                            </div>
                            {errors.consumerKey && <small className="text-danger"><i className="fa fa-exclamation-circle"></i> {errors.consumerKey}</small>}
                        </div>
                        <div className='parent'>
                            <div className="form-input">
                                <input type="text" name="consumerSecret" placeholder="Consumer Secret"  onChange={handleInput}></input>
                            </div>
                            {errors.consumerSecret && <small className="text-danger"><i className="fa fa-exclamation-circle"></i> {errors.consumerSecret}</small>}
                        </div>
                        <div className='parent'>
                            <div className="form-input">
                                <input type="text" name="confirmationUrl" placeholder="Confirmation Url"  onChange={handleInput}></input>
                            </div>
                            {errors.confirmationUrl && <small className="text-danger"><i className="fa fa-exclamation-circle"></i> {errors.confirmationUrl}</small>}
                        </div>
                       <div className='parent'>
                            <div className="form-input">
                                <input type="text" name="validationUrl" placeholder="Validation Url"  onChange={handleInput}></input>
                            </div>
                            {errors.validationUrl && <small className="text-danger"><i className="fa fa-exclamation-circle"></i> {errors.validationUrl}</small>}
                       </div>
                       {
                            loading 
                            ? 
                                <Bars color={'#228b22'} height="80" width="80" ariaLabel='bars-loading' visible={loading}/> 
                            : 						
                                <button type="submit" className="btn btn-primary theme-button mt-4">REGISTER</button>
                        }
					</form>
					<p className="signup"> Devme Technologies</p>
				</div>
			</div>
		</div>
		<div id='stars'></div>
		<div id='stars2'></div>
		<div id='stars3'></div>
		<div className="copy-right">
			<p>&copy; {new Date().getFullYear()}  All rights reserved | Design by <a href="http://tevinly.herokuapp.com/"  rel="noreferrer" target="_blank">Devme</a></p>
		</div>
	</section>
    )
}

export default Form;
