import { isWebUri } from "valid-url"

export default function Validation(values){
    const errors={}
    if(values.payBill ===""){
        errors.payBill="Bussiness Short Code is required"
    }else if(values.payBill < 0){
        errors.payBill="Enter a valid Bussiness Short Code."  
    }

    if(values.consumerKey ===""){
        errors.consumerKey="Consumer key is required"
    }
    
    if(values.consumerSecret ===""){
        errors.consumerSecret="Consumer secret is required"
    }
    
    if(values.confirmationUrl ===""){
        errors.confirmationUrl="Confirmation url is required"
    }else if(!isWebUri(values.confirmationUrl)){
        errors.confirmationUrl="Enter a valid confirmation url"
    }
    
    if(values.validationUrl ===""){
        errors.validationUrl="Validation url is required"
    }else if(!isWebUri(values.validationUrl)){
        errors.confirmationUrl="Enter a valid validation url"
    }
    return errors
}