import React from "react";
import Form from "./form/Form";
import NoPage from "./NoPage/NoPage"
import { BrowserRouter,Routes,Route} from "react-router-dom";

function App(){
  return (
    <BrowserRouter>
      <Routes>
        <Route exact path="/" element={<Form/>} />
        <Route path="*" element={<NoPage/>}/>
      </Routes>
    </BrowserRouter>
  );
}
export default App;
